'use strict'

import 'normalize.css/normalize.css'
import $ from 'jquery'
import _ from 'lodash'
import Masonry from 'masonry-layout'

import './styles/style.scss'

var modalState = {
    modal: null,
    lastInvokedId: null
}

$.when($.ready).then(function () {
    makeSchedule()
    initTabs()
})

const shortMonthFormat = new Intl.DateTimeFormat('ru-RU', {
    month: 'short'
})

const dateFormat = new Intl.DateTimeFormat('ru-RU', {
    month: 'long',
    day: 'numeric',
    hour: 'numeric',
    minute: 'numeric',
    weekday: 'long',
    year: 'numeric'
})

const addresses = {
    1: '.  , 41',
    2: '. , 13',
    3: '.  , 231',
    4: '.  , 95',
    5: ' -, 2/4,   17',
    6: '. , 885'
}

function initTabs() {
    $('.modal-about-tab').click(function () {
        var tab = $(this)

        if (tab.hasClass('disabled')) {
            return
        }

        switchTab(tab)
    })
}

function switchTab(tab) {
    $('.modal-about-tab.current').removeClass('current')
    tab.addClass('current')

    const idx = tab.index()

    $('.modal-about-tab-body.current').removeClass('current')
    $('.modal-about-tab-body').eq(idx).addClass('current')

    if (tab.hasClass('description-tab')) {
        $('.modal-teacher').show()
    } else {
        $('.modal-teacher').hide()
    }
}

function makeSchedule() {
    $.ajax({
        url: 'https://db2.gekkon-club.ru/api/calendar',
        data: {
            from: dateForApi(),
            type: 1,
            period: 30
        },
        dataType: 'json'
    })
        .done(createScheduleBlock)
        .fail(createErrorBlock)
}

function dateForApi() {
    const current = new Date()
    var day, month, year

    day = current.getDate()
    if (day < 10) day = '0' + day

    month = current.getMonth() + 1
    if (month < 10) month = '0' + month

    year = current.getFullYear()

    return `${day}-${month}-${year}`
}

function createScheduleBlock(apiItems) {
    for (const item of apiItems) {
        const data = extractData(item)
        var masterCard = $(makeMasterCard(data))
        masterCard.data('item', item)
        $('.mc-cards').append(masterCard)
    }

    initListeners().then(function () {
        modalState.modal = $('.enroll-modal').clone(true)
    })

    var grid = new Masonry('.mc-cards', {
        fitWidth: true,
        itemSelector: '.mc-card',
        transitionDuration: 0,
        gutter: 10,
        horizontalOrder: true,
        initLayout: false
    })

    grid.layout()
}

function createErrorBlock() {
    const errorBlock = $(`<div class="mc-error">
                          ,  -   ,
                             
                    </div>`)
    $('.master-classes').append(errorBlock)
}

function extractData(apiItem) {
    const cover = extractCover(apiItem)
    const title = extractTitle(apiItem)
    const age = extractAge(apiItem)
    const theme = chooseTheme(apiItem)
    const id = apiItem.id
    const duration = apiItem.baseLesson.duration
    const date = extractDate(apiItem.startDate)
    const teacher = apiItem.teacher
    const address = extractAddress(apiItem)
    const aboutData = {
        description: apiItem.description,
        teacher: teacher ? teacher.bio : null
    }

    return {
        day: date.day,
        weekday: date.weekday,
        month: date.month,
        year: date.year,
        time: date.time,
        shortMonth: date.shortMonth,
        theme,
        cover,
        title,
        age,
        address,
        id,
        duration,
        aboutData,
        teacher
    }
}

function extractAge(apiItem) {
    const lessonInfo = apiItem.baseLesson
    var age

    if (!(lessonInfo.minClass || lessonInfo.minAge)) {
        age = ''
    } else if (lessonInfo.minClass) {
        age = `${lessonInfo.minClass}-${lessonInfo.maxClass} .`
    } else {
        age = `${lessonInfo.minAge}-${lessonInfo.maxAge} `
    }

    return age
}

function extractTitle(apiItem) {
    const lessonInfo = apiItem.baseLesson

    return lessonInfo.name.trim()
        .split(' ')
        .slice(1, -1)
        .join(' ')
}

function chooseTheme(apiItem) {
    var theme
    const lesson = apiItem.baseLesson

    if (lesson.minClass === null && lesson.minAge === null) {
        theme = 'no-age'
    } else if (lesson.minAge && !(lesson.minClass)) {
        theme = 'preschoolars'
    } else if (lesson.minClass <= 2) {
        theme = 'grades1-2'
    } else if (lesson.minClass <= 4) {
        theme = 'grades3-4'
    } else {
        theme = 'grades5-11'
    }

    return theme
}

function extractCover(apiItem) {
    const cover = apiItem.cover || 'img/cover-default.jpg'

    return cover
}

function extractDate(date) {
    date = new Date(date)
    const dateParts = dateFormat.formatToParts(date)
    const shortMonth = shortMonthFormat.format(date)

    var dateObject = {}

    for (const part of dateParts) {
        if (part.type !== 'literal') {
            dateObject[part.type] = part.value
        }
    }

    dateObject.time = dateObject.hour + ':' + dateObject.minute
    dateObject.shortMonth = shortMonth.slice(0, -1)

    return dateObject
}

function extractAddress(apiItem) {
    return addresses[apiItem.room.affiliate_id]
}

function makeMasterCard(data) {
    const cardTemplate = `
    <div class="mc-card ${data.theme}">
        <button class="more theme"></button>
        <img class="mc-cover" src="${data.cover}" alt="">
        <div class="mc-footer">
            <div class="mc-date theme">
                <div class="day">${data.day}</div>
                <div class="month">${data.shortMonth}</div>
            </div>
            <div class="mc-description">
                <div class="title">${data.title} ${data.age}</div>
                <div class="time">${data.time}</div>
                <div class="address">${data.address}</div>
            </div>
        </div>
    </div>
    `

    return cardTemplate
}

function initListeners() {
    return new Promise(function (resolve) {
        $('.modal-close').click(function () {
            $(this).parent().parent().removeClass('open')
        })

        $('.more').click(function () {
            const rawData = $(this).parent().data('item')
            const data = extractData(rawData)

            if (data.id !== modalState.lastInvokedId) {
                modalState.lastInvokedId = data.id
                $('.enroll-modal').replaceWith(modalState.modal)
                modalState.modal = modalState.modal.clone(true)
            }

            fillModal(data)
            $('.enroll-modal-box').addClass('open')
        })

        $('.show-form').click(function () {
            $(this).parent().slideUp()
            $('.modal-enroll').slideDown()
        })

        $('.modal-enroll-form').submit(function (e) {
            e.preventDefault()

            $.post('https://db2.gekkon-club.ru/api/add-lead', $(this).serialize())
                .done(function () {
                    $('.modal-enroll-form').hide()
                    $('.enroll-success').show()
                })
                .fail(function (xhr, status) {
                    console.log(status, xhr)
                })
        })

        $('.enroll-input-field').on('keyup', function () {
            const field = $(this)
            if (field.val()) {
                field.addClass('filled')
            } else {
                field.removeClass('filled')
            }
        })

        resolve('initiated')
    })
}

function autoCompletion(input) {
    
}

function fillModal(data) {
    fillMeta(data)
    fillHeader(data)
    fillInfo(data)
    fillAbout(data.aboutData)
    fillTeacher(data.teacher)
}
function fillMeta(data) {
    $('.enroll-modal').addClass(data.theme)
    $('.modal-enroll-form input[name="lessonId"]').val(data.id)
}

function fillHeader(data) {
    $('.modal-header-cover').attr('src', data.cover)
    $('.modal-header-title').text(data.title)

    if (data.age) {
        $('.header-extra-item.age span').text(data.age)
    } else {
        $('.header-extra-item.age').hide()
    }

    if (data.duration) {
        $('.header-extra-item.duration span').text(data.duration + ' ')
    } else {
        $('.header-extra-item.duration').hide()
    }
}

function fillInfo(data) {
    const fullDate = `${data.day} ${data.month} ${data.year}, ${data.weekday}`

    $('.event-info-item.date').text(fullDate)
    $('.event-info-item.address').text(data.address)
    $('.event-info-item.time').text(data.time)
}

const contentFillers = {
    description(text) {
        $('.description-tab-body').text(text)
    },
    teacher(bio) {
        $('.teacher-tab-body').text(bio)
    }
}

const tabs = [
    'video',
    'photo',
    'teacher',
    'description'
]

function fillAbout(aboutData) {
    if (_.some(_.values(aboutData), Boolean)) {
        for (const tabName of tabs) {
            const tabValue = aboutData[tabName]
            if (tabValue) {
                const fillWithContent = contentFillers[tabName]
                fillWithContent(tabValue)
                switchTab($('.' + tabName + '-tab'))
            } else {
                $('.' + tabName + '-tab').addClass('disabled')
            }
        }
    } else {
        $('.modal-about').hide()
    }
}

function fillTeacher(teacher) {
    if (teacher) {
        const avatar = teacher.avatar || 'img/avatar-default.png'
        $('.modal-teacher-name').text(teacher.name)
        $('.modal-teacher-photo').attr('src', avatar)
    } else {
        $('.modal-teacher').hide()
    }
}
